body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.badBin {
    text-align: center;
    padding: 10px;
    margin: 40px;
    background-color: rgb(245, 69, 69);
    color:white;
}

.goodBin{
    text-align: center;
    padding: 10px;
    margin: 40px;
    background-color: rgb(101, 228, 101);
    color:white;
}

.DottedBox {
    margin: 40px;
    border: 5px dotted pink;
}

.DottedBox_Content {
    font-size:15px;
    text-align: centre;
}

.section {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.container {
    width: 100%;
    max-width: 300px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: rgb(186, 193, 207);
  }
  
  li {
    float: left;
  }
  
  li a {
    display: inline-block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  li a:hover {
    background-color: rgb(165, 178, 202);
  } 

.active {
    background-color: rgb(138, 150, 170);
}

